.header {
  position: fixed;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #c4c4c4;
  width: 100%;
  z-index: 10;
}

.headerContent {
  position: relative;
  max-width: 1400px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.headerContent > .logo {
  flex: 0 1 auto;
}

.logo {
  display: flex;
  font-size: 1.4rem;
  font-weight: bold;
  color: black;
  text-decoration: none;
}

.headerSecond {
  display: flex;
  top: 110px;
  justify-content: space-between;
  align-items: center;
  background-color: #c4c4c4;
  height: 70px;
  position: absolute;
  left: 0;
  width: 100%;
  padding: 10px;
  z-index: 20;
}

.searchForm {
  margin-left: 20px;
}

.containerMenu {
  text-align: center;
  justify-content: space-between;
  padding: 10px;
  display: flex;
  flex-direction: row;
  transition: all 0.3s ease-in-out; /* Para la transición suave del menú */
}

.containerMenu ul {
  list-style: none;
  display: flex;
  margin: 0;
  padding-top: 5px;
}

.containerMenu li {
  margin-right: 10px; /* Espacio entre elementos */
}

.containerMenu li:last-child {
  margin-right: 0; /* Elimina el espacio después del último elemento */
}

.containerMenu a {
  text-decoration: none;
  color: #ffffff;
  padding: 10px;
  border: 1px solid #ccc;
  background-color: #091e4b;
}

.containerMenu a:hover {
  background-color: #f0f0f0;
  color: black;
}

.dropdown {
  position: relative;
}

.superMenu1 {
  display: none; /* Oculto por defecto */
  position: absolute;
  top: 120%; /* Aparece justo debajo del menú principal */
  left: 0;
  background-color: #c4c4c4;
  padding: 10px;
  white-space: nowrap;
  z-index: 30;
}

.superMenu1 ul {
  display: flex; /* Muestra los elementos en línea */
  flex-direction: row;
  gap: 10px; /* Espaciado entre elementos */
}

.superMenu1 a {
  display: block;
  padding: 10px;
  color: rgb(255, 255, 255);
  background-color: #091e4b;
  text-decoration: none;
  border: 1px solid #ccc;
}

.superMenu1 a:hover {
  background-color: #f0f0f0;
}

.superMenu1 li {
  margin-right: 20px; /* Espacio entre los elementos del menú */
  display: inline-block; /* Esto puede ser opcional dependiendo de la estructura */
}

.superMenu1 li:last-child {
  margin-right: 0; /* El último elemento no tendrá margen derecho */
}

.closeButton {
  background-color: transparent; /* Fondo transparente */
  border: none; /* Sin bordes */
  color: #333; /* Color del icono */
  font-size: 1.5rem; /* Tamaño del icono */
  cursor: pointer; /* Cambia el cursor al pasar por encima */
  right: 10px;
  transition: color 0.3s ease;
}
